<template>
  <div class="myCollection">
    <headers :title="title"></headers>
    <div class="main">
      <div class="dhnw"  v-for="obj of csDetail.contents" :key="obj.id">
        <p><span><img :src="obj.iconsUrl" alt="">{{obj.shopName}}</span><i>{{obj.createTime}}</i></p>
        <div class="msg">
          <img :src="obj.shopIconsUrl" alt="">
          <span>{{obj.title}}</span>
        </div>
      </div>
      <div class="noCollection" v-if="noCollection">
        <img src="./images/collection_no@2x.png" alt="">
        <p>您还没有收藏哦~</p>
      </div>
    </div>
  </div>
</template>

<script>
  import 'lib-flexible'
  import headers from '../../components/editHeader.vue'
  export default {
    name: 'MyCollection',
    components: {
      headers
    },
    data() {
      return {
        title: "我的收藏",
        csDetail: {},
        noCollection: false
      }
    },
    created () {
      this.getCollection();
    },

    beforeUpdate () {
      let _this = this;
      if(_this.csDetail.contents.length==0){
        _this.noCollection == true
      }

    },
    updated () {
      let _this = this;

    },
    methods: {
      onClickLeft() {

      },
      getCollection(){
        let pType = 2;
        let _this = this;
        this.$get('guanjia_heard/shopping/getCsByType',{pType}).then((res)=>{
          console.log({res})
          _this.csDetail = res.data.csDetail

        })
      }
    }
  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .myCollection {
    width: 100%;
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  .main {
    flex: 1;
    width: 100%;
    background: #f6f6f6;
  }

  .dhnw {
    margin-top: 110px;
    width: 100%;
    background: #fff;
    padding-left: 30px;
    box-sizing: border-box;
  }

  .dhnw p {
    height: 102px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 28px;
    border-bottom: 1px solid #f6f6f6;
  }

  .dhnw img {
    width: 58px;
    height: 58px;
    margin-right: 24px;
  }

  .dhnw p i {
    font-size: 24px;
    font-style: normal;
    padding-right: 24px;
    color: #777;
  }

  .msg {
    display: flex;
    align-items: top;
    padding: 24px 0;
    color: #000;
  }

  .msg img {
    width: 120px;
    height: 120px;
    margin-right: 18px;
  }

  .msg span {
    margin-left: 10px;
    width: 405px;
    font-size: 30px;
  }

  .noCollection {
    padding-top: 200px;
    text-align: center;
  }

  .noCollection img {
    width: 180px;
  }

  .noCollection p {
    font-size: 28px;
    line-height: 2.5;
    color: #c4c4c4;
  }
</style>



